.interests {
  margin-left: 4rem;
  margin-top: 3.5rem;
  margin-bottom: 2.5rem;
  width: 80%;
  text-align: justify;
  
}

.card {
  margin: 0.8rem;
  padding: 1.2rem;
  text-align: left;
  color: inherit;
  text-decoration: none;
  border: 1px solid #eaeaea;
  border-radius: 10px;
  transition: color 0.15s ease, border-color 0.15s ease;
  max-width: 900px;
}

.card:hover,
.card:focus,
.card:active {
  color: #0070f3;
  border-width: 8px;
  border-color: #0070f3;
}

@media screen and (max-width: 640px) {
  .interests {
    justify-content: space-between;
    margin-left: 2px;
    font-size: 0.8rem;
    width: 60%;
    zoom: 1;
  }

  a {
    width: 50%;
    font-size: 0.8rem;
  }
}

@media only screen and (max-width: 1110px) {
  .interests {
    width: 70%;
    margin-top: 0rem;
    margin-bottom: 0rem;
    margin-left: 1.5rem;
    padding: 0.5rem;
    font-size: 1.1rem;
  }

  a {
    font-size: 1.1rem;
  }
}