* {
  box-sizing: border-box;
  max-width: 100%;

}

html {
  background: #191919;
  color: white;
  font-family: sans-serif;
  display: block;
  scroll-behavior: smooth;
}

h1 {
  font-size: 3rem;
}

body {
  margin: 0;
  display: flex;
  font-size: 1.3rem;
}

a {
  font-size: 1.4rem;
  text-decoration: none;
  color: #fff;
  padding: 1rem;
}

