.header {
    height: 2.8rem;
    align-items: center;
    position: fixed;
    padding: 10px;
    background: black;
    color: white;
    width: 100%;
    display: flex;
    justify-content: space-between;
    z-index: 10;
    overflow: hidden;
}

.welcome {
    margin: 0.7rem;
    padding: 0.1rem;
    font-weight: 700;
    font-size: 2rem;
    padding-left: 5px;
    padding-right: 5px;
    border: 5px solid #0070f3;
    border-radius: 10px;
    animation: blinker 1s step-start infinite;
}

@keyframes blinker {
    50% {
        opacity: 0;
    }
}

.card {
    margin: 3px;
    padding-top: 2px;
    padding-bottom: 2px;
    padding-left: 5px;
    padding-right: 5px;
    text-align: center;
    color: inherit;
    text-decoration: none;
    border: 1px solid #eaeaea;
    border-radius: 10px;
    transition: color 0.15s ease, border-color 0.15s ease;
    max-width: 500px;
    overflow: hidden;
}

.card:hover,
.card:focus,
.card:active {

    border-width: 5px;
    border-color: #0070f3;
}

.headerlist {
    display: flex;
}

.headerlist div {
    padding: 0 1rem;
}

.headerlist div a {
    font-size: 1.2rem;
    font-weight: 500;
}

.hamburger {
    display: none;
}

@media screen and (max-width: 1040px) {

    .headerlist {
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 50%;
        right: -100%;
        z-index: -3;
        transition: 0.4s;
    }

    .headerlistActive {
        right: 0;
        width: 40%;
        height: 100%;
        position: fixed;
        margin-right: 0;
        z-index: 1;
        top: 0;
        right: 100;
        background-color: #111;
        overflow-x: hidden;
        opacity: 0.9;
        padding: 20px;
    }

    .headerlist li {
        padding: 1rem 0;
    }

    .headerlist li a {
        font-size: 1.2rem;
        padding: 0;
    }

    .hamburger {
        display: initial;
    }
}